import React, { FunctionComponent } from 'react'
import { Link, graphql } from 'gatsby'

import Styles from '../styles/templates/post-detail.module.styl'
import Layout from '../components/layout'
import { rhythm, scale } from '../utils/typography'
import { Site, MarkdownRemark } from '../graphql-types'
import classnames from 'classnames'
import { GitComment } from '../components/git-comment/gitComment'

type Props = {
  data: {
    markdownRemark: MarkdownRemark,
    site: Site
  },
  pageContext: {
    previous: null | MarkdownRemark,
    next: null | MarkdownRemark
  },
  location: Location
}

const BlogPostTemplate: FunctionComponent<Props> = (props) => {
  const post = props.data.markdownRemark
  const siteTitle = props.data.site.siteMetadata.title // graphql 查询的数据
  const { previous, next } = props.pageContext // 由 createPage context 传递过来的数据
  return (
    <Layout post={post} location={props.location} title={siteTitle}>
      <div className='post-detail-container'>
        <article className={Styles['article']}>
          <header>
            <h1
              style={{
                marginTop: rhythm(1),
                marginBottom: 0
              }}
            >
              {post.frontmatter.title}
            </h1>
            <p
              style={{
                ...scale(-1 / 5),
                display: `block`,
                marginBottom: rhythm(1)
              }}
            >
              {post.frontmatter.date}
            </p>
          </header>
          <section dangerouslySetInnerHTML={{ __html: post.html }} />
        </article>
        <div className={classnames(Styles['bottom'], { [Styles['havetwo']]: previous && next })}>
          {previous && (
            <Link className={Styles['pre']} to={previous.fields.slug} rel="prev">
              <i className={classnames(Styles['icon'], 'iconfont', Styles['left'])}>&#xe65d;</i> <span className={Styles['title']}>{previous.frontmatter.title}</span>
            </Link>
          )}
          {next && (
            <Link className={Styles['next']} to={next.fields.slug} rel="next">
              <span className={Styles['title']}>{next.frontmatter.title}</span><i className={classnames('iconfont', Styles['icon'])}>&#xe65d;</i>
            </Link>
          )}
        </div>
        <GitComment />
      </div>
    </Layout>
  )
}

export default BlogPostTemplate
// 可以接受 createPage 中的 context 作为参数
// $slug 为 context 中的 slug
export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
      }
    }
  }
`
